.input_input-form_l9eYg {
    height: 2rem;
    padding: 0 0.75rem;

    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 0.625rem;
    font-weight: bold;
    color: hsla(225, 15%, 40%, 1);

    border-width: 1px;
    border-style: solid;
    border-color: hsla(0, 0%, 0%, 0.15);
    border-radius: 2rem;

    outline: none;
    cursor: text;
    transition: 0.25s ease-out; /* @todo: standardize with var */
    box-shadow: none;

    /*
        For truncating overflowing text gracefully
        Min-width is for a bug: https://css-tricks.com/flexbox-truncated-text
        @todo: move this out into a mixin or a helper component
    */
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    min-width: 0;
}

.input_input-form_l9eYg:hover {
    border-color: hsla(215, 100%, 65%, 1);
}

.input_input-form_l9eYg:focus {
    border-color: hsla(215, 100%, 65%, 1);
    box-shadow: 0 0 0 0.25rem hsla(215, 100%, 65%, 0.35);
}

